@font-face {
  font-family: "America Regular";
  src: url("../fonts/GT-America-Standard-Regular.woff2") format("woff2"), url("../fonts/GT-America-Standard-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

@font-face {
  font-family: "America Bold";
  src: url("../fonts/GT-America-Standard-Bold.woff2") format("woff2"), url("../fonts/GT-America-Standard-Bold.woff") format("woff");
  font-weight: normal;
  font-style: normal;
}

html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5,
h6, p, blockquote, pre, a, abbr, acronym, address, big,
cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small,
strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt,
dd, ol, ul, li, fieldset, form, label, legend, table, caption,
tbody, tfoot, thead, tr, th, td, article, aside, canvas, details,
embed, figure, figcaption, footer, header, hgroup, menu, nav,
output, ruby, section, summary, time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  vertical-align: baseline;
}

html {
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

img, fieldset, a img {
  border: none;
}

input[type="text"],
input[type="email"],
input[type="tel"],
textarea {
  -webkit-appearance: none;
}

input[type="submit"],
button {
  cursor: pointer;
}

input[type="submit"]::-moz-focus-inner,
button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

textarea {
  overflow: auto;
}

input, button {
  margin: 0;
  padding: 0;
  border: 0;
}

div, input, textarea, select, button,
h1, h2, h3, h4, h5, h6, a, span, a:focus {
  outline: none;
}

ul, ol {
  list-style-type: none;
}

table {
  border-spacing: 0;
  border-collapse: collapse;
  width: 100%;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    transition: none !important;
  }
}

.icon-facebook {
  width: 0.53em;
  height: 1em;
  fill: #000;
}

body {
  padding-top: 52px;
  color: #333;
  background: #FFF;
  font-family: "America Regular", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 14px;
  line-height: 1.28571;
}

.logo {
  display: inline-block;
  vertical-align: top;
  color: #333 !important;
  font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 24px;
  line-height: 50px;
  text-decoration: none !important;
}

@media only screen and (max-width: 599px) {
  .logo {
    font-size: 18px;
  }
}

h1, h2, h3, h4, h5, h6,
strong, em, b, th, cite {
  font-weight: normal;
}

strong, b {
  font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
}

.text {
  font-family: "America Regular", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 1.5;
}

.block {
  display: flex;
}

.header {
  position: fixed;
  z-index: 101;
  top: 0;
  left: 0;
  right: 0;
  border-bottom: 2px solid #dedede;
  background: #fff;
}

.header:after {
  content: " ";
  display: table;
  clear: both;
}

.header__logo {
  float: left;
  padding: 0 20px;
}

@media only screen and (max-width: 599px) {
  .header__logo {
    padding: 0 15px;
  }
}

.header__nav {
  float: right;
}

@media only screen and (max-width: 767px) {
  .header__nav {
    position: fixed;
    top: 52px;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: auto;
    padding: 0 20px;
    opacity: 0;
    visibility: hidden;
    background: #fff;
    transition: opacity 0.225s ease-in, visibility 0.225s ease-in;
  }
}

@media only screen and (max-width: 767px) {
  .header__nav.is-open {
    opacity: 1;
    visibility: visible;
  }
}

.header__btn {
  float: left;
  padding: 0 20px;
  border-left: 2px solid #dedede;
  color: #333;
  font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 50px;
}

.no-touch .header__btn:hover {
  color: #333;
  text-decoration: underline;
}

@media only screen and (max-width: 767px) {
  .header__btn {
    float: none;
    display: block;
    padding: 0 20px 0 65px;
    border-left: none;
    border-bottom: 1px solid #dedede;
    width: 100%;
    font-size: 18px;
    line-height: 60px;
    text-transform: uppercase;
  }
}

.header__nav-btn {
  float: right;
  display: none;
  border-left: 2px solid #dedede;
  width: 50px;
  height: 50px;
  background: none;
}

@media only screen and (max-width: 767px) {
  .header__nav-btn {
    display: block;
  }
}

.header__nav-btn.is-active span:before {
  transform: translate3d(4px, -2px, 0) rotate(45deg);
}

.header__nav-btn.is-active span:after {
  transform: translate3d(2px, 2px, 0) rotate(-45deg);
}

.header__nav-btn.is-active span i {
  opacity: 0;
  transform: translate3d(-70%, 0, 0) scaleX(0.5);
}

.header__nav-btn span {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  width: 25px;
  height: 17px;
}

.header__nav-btn span:before, .header__nav-btn span:after {
  content: '';
  transform-origin: left top;
  transition: transform 0.2s ease-in;
}

.header__nav-btn span:before, .header__nav-btn span:after,
.header__nav-btn span i {
  position: absolute;
  left: 0;
  border-top: 3px solid #333;
  width: 100%;
}

.header__nav-btn span:before {
  top: 0;
}

.header__nav-btn span:after {
  bottom: 0;
}

.header__nav-btn span i {
  top: 50%;
  margin-top: -1px;
  transition: transform 0.2s ease-in, opacity 0.2s ease-in;
}

.sm-search {
  position: relative;
  float: left;
  width: 400px;
}

@media only screen and (max-width: 1023px) {
  .sm-search {
    width: 300px;
  }
}

@media only screen and (max-width: 767px) {
  .sm-search {
    margin-bottom: 20px;
    width: 100%;
  }
}

.sm-search.is-focus .sm-search__field {
  transform: translate3d(0, 0, 0);
}

.sm-search.is-focus .sm-search__dropdown {
  opacity: 1;
  visibility: visible;
}

.sm-search__container {
  overflow: hidden;
}

@media only screen and (max-width: 767px) {
  .sm-search__container {
    border-bottom: 2px solid #dedede;
  }
}

.sm-search__field {
  position: relative;
  border-left: 2px solid #dedede;
  transform: translate3d(50%, 0, 0);
  transition: transform 0.225s ease-in;
}

@media only screen and (max-width: 1023px) {
  .sm-search__field {
    transform: translate3d(0, 0, 0);
  }
}

@media only screen and (max-width: 767px) {
  .sm-search__field {
    border-left: none;
  }
}

.sm-search__input {
  position: relative;
  display: block;
  padding: 0 20px 0 55px;
  width: 100%;
  height: 50px;
  color: #333;
  font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  background: none;
  line-height: normal;
}

@media only screen and (max-width: 767px) {
  .sm-search__input {
    padding-left: 65px;
    height: 70px;
    font-size: 18px;
  }
}

.sm-search__icon {
  position: absolute;
  top: 50%;
  left: 20px;
  margin-top: -9px;
  font-size: 18px;
}

@media only screen and (max-width: 767px) {
  .sm-search__icon {
    margin-top: -12px;
    font-size: 24px;
  }
}

.sm-search__dropdown {
  position: absolute;
  top: 100%;
  left: 0;
  right: -2px;
  margin-top: 2px;
  border: 2px solid #dedede;
  border-top: none;
  opacity: 0;
  visibility: hidden;
  background: #fff;
  transition: opacity 0.225s ease-in, visibility 0.225s ease-in;
}

@media only screen and (max-width: 767px) {
  .sm-search__dropdown {
    display: none;
  }
}

.sm-search__list {
  overflow: auto;
  max-height: 200px;
}

.sm-search__item {
  padding: 10px 20px;
  font-size: 14px;
  line-height: 20px;
}

.sm-search__item:not(:first-child) {
  border-top: 1px solid #dedede;
}

.sm-search__img {
  float: left;
  margin-top: 2px;
  margin-right: 15px;
  border-radius: 50%;
  width: 36px;
  height: 36px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.sm-search__title {
  font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
}

.sm-search__content {
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}

.sm-search__more {
  display: block;
  padding: 0 20px;
  border-top: 2px solid #dedede;
  color: #F96854 !important;
  font-family: "America Bold", Helvetica Neue, Helvetica, Arial, sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 50px;
}

.no-touch .sm-search__more:hover {
  opacity: 0.9;
}

.post {
  padding: 20px 0;
  margin: 0 auto;
  max-width: 712px;
}

.post__frame {
  position: relative;
  padding-bottom: 56.25%;
  margin-bottom: 20px;
  width: 100%;
  height: 0;
}

.post__frame iframe {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 100%;
}
